$columns: 18;
$offset: 30px;
$offset_one_side: ($offset / 2);
$atom: (100% / $columns);

$break_bigger: 1700px;
$break_lg_1600: 1600px;
$break_lg_1500: 1500px;
$break_lg_1400: 1400px;
$break_lg_1300: 1300px;
$break_lg_1200: 1200px;
$break_lg: 1100px;
$break_md: 980px;
$break_mdd: 960px;
$break_sm: 768px;
$break_sm_767: 767px;
$break_xs: 560px;
$break_xxs: 480px;

@mixin reset(){
    body,div,dl,dt,dd,ul,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,input,textarea,p,blockquote,th,td{
        margin: 0;
        padding: 0;
    }
    table{
        border-collapse: collapse;
        border-spacing: 0;
    }
    fieldset,img,abbr{
        border: 0;
    }
    address,caption,cite,code,dfn,em,strong,th,var{
        font-style: normal;
        font-weight: normal;
    }
    ul li{
        list-style: none;
    }
    caption,th{
        text-align: left;
    }
    h1,h2,h3,h4,h5,h6{
        font-size: 100%;
        font-weight: normal;
    }
    sup{
        vertical-align: text-top;
    }
    sub{
        vertical-align: text-bottom;
    }
    input,textarea,select{
        font-family: inherit;
        font-size: inherit;
        font-weight: inherit;
    }
    legend{
        color: #000;
    }
    article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section,main{
        display: block;
    }
    img{
        max-width: 100%;
        height: auto;
    }
}

@mixin wrapper(){
    max-width: 1775px;
    padding-left: 15px;
    padding-right: 15px;
    margin: 0 auto;

    @media screen and (max-width: $break_bigger){
